import React from "react";
import { MailIcon } from "@heroicons/react/solid";

const TextHeader: React.FC = (): JSX.Element => {
  //   const [showModal, setShowModal] = React.useState(false);
  //   const [openstyle, setOpenstyle] = React.useState(false);
  //   const [open, setOpen] = React.useState(false);

  return (
    <div className="flex">
      {/* //////// */}
      <div className="">
        <div className="p-4">
          <svg
            width="25"
            height="25"
            viewBox="0 0 25 25"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            xmlnsXlink="http://www.w3.org/1999/xlink"
          >
            <rect width="25" height="25" fill="url(#pattern0)" />
            <defs>
              <pattern
                id="pattern0"
                patternContentUnits="objectBoundingBox"
                width="1"
                height="1"
              >
                <use xlinkHref="#image0" transform="scale(0.005)" />
              </pattern>
              <image
                id="image0"
                width="200"
                height="200"
                xlinkHref="data:image/png;base64,"
              />
            </defs>
          </svg>
        </div>
      </div>
      {/* ///////// */}
      <div className="py-4 ml-6 border-r-2 border-bordercolor"></div>
      <div className="mt-2 ml-3">
        <button
          type="button"
          className="inline-flex items-center px-3 py-2 text-sm font-medium leading-4 text-white border border-transparent rounded-md shadow-sm bg-indigo600 hover:bg-indigo700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo500"
        >
          <MailIcon className="w-4 h-4 mr-2" aria-hidden="true" />
          Button text
        </button>
      </div>
    </div>
  );
};
export default TextHeader;
